import React, { useState, useEffect } from 'react'; // useState와 useEffect를 import합니다.
import { Container, Row, Col, Button } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { addCount, subtractcount,removeItem,handleRemove} from '../store'; // sortName 제거
import { useParams } from 'react-router-dom';
import '../Cart.css';

const Cart = () => {
  const { id } = useParams(); // 사용하지 않는 변수입니다.
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.cart);

  const [shopping, setShopping] = useState(3000);
  const [totalOrderAmount, setTotalOrderAmount] = useState(0); // 총 주문금액 상태 추가

  // 총 주문금액 계산 함수
  const calculateTotalSum = () => {
    let sum = 0;
    for (let v of cart) {
      sum +=  v.price * v.amount;
    }
    return sum;
  };

  useEffect(() => {
    const sum = calculateTotalSum();
    setTotalOrderAmount(sum); // 장바구니의 총 주문금액을 업데이트합니다.

    if (sum <= 0) {
      setShopping(0);
    } else {
      setShopping(3000);
    }
  }, [cart]); // cart 배열이 변경될 때마다 useEffect가 실행됩니다.

  // cart 총 수량
  const totalAmount = cart.reduce((acc, item) => acc + item.amount, 0);




  
  //숫자단위 콤마
  function formatPrice(price) {
    return new Intl.NumberFormat('ko-KR', { style: 'currency', currency: 'KRW' }).format(price);
  }


//상품 삭제

  // const handleRemove = (id) => {
  //   dispatch(removeItem(id)); 
  // };


  return (
    <div className='cartwrap'>
      <Container className="cart-list">
        <div id='detail_text'>
          <h5>CART({totalAmount})</h5>
        </div>
        <Row>
          {/* 장바구니 항목 렌더링 부분은 생략합니다. */}
          <Col md={8}>
            {cart.length === 0 ? ( // 조건부 렌더링을 삼항 연산자로 변경
              <p className='shop-null'>장바구니가 비어있습니다.</p>
            ) : (
              cart.map((v, i) => (
                <div key={i} className='cart_detail'>
                  <div className='detail-wrap'>
                    <img src={process.env.PUBLIC_URL + v.imgUrl} alt="" width="140px" />
                    {/*  */}
                    <div className='detail-flex-wrap'>
                      <span>{v.item}</span>
                      <span className='price'>{formatPrice(v.price)}</span>
                      <span className='button'>
                        <button onClick={() => dispatch(subtractcount(v.id))}>-</button>
                        {v.amount}
                        <button onClick={() => dispatch(addCount(v.id))}>+</button>
                      </span>
                    </div>
                  </div>
                    <Button Button variant="dark"  onClick={() => dispatch(removeItem(v.id))}className='delete-btn'>X</Button>
                </div>
              ))
            )}
          </Col>


          <Col id='detail-right-wrap' md={4}>
            <div className='detail-right-wrap'>
              <h4>결제 정보</h4>
              <div className='total_list'>
                <div className='total_order'>
                  <p>총 주문금액 : {formatPrice(totalOrderAmount)}</p>
                  <p>배송비 : {formatPrice(shopping)}</p>
                </div>
                <h4 className='total_amount'>
                  결제금액 : {formatPrice(Number(totalOrderAmount)+shopping)}
                </h4>
              </div>
              <Button variant="dark" size="lg">CHECKOUT</Button>
            </div>
          </Col>
        </Row>
      </Container>
      <div id="nekoicon"><a href="#"><img src={ process.env.PUBLIC_URL +'/img/neko.png'} /></a></div>
    </div>
    
  );
};

export default Cart;