import React from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import { useNavigate, useParams } from 'react-router-dom';
import { addItem } from '../store';
import { useDispatch } from 'react-redux';

const Detail = (props) => {
    let { phone } = props;    
    let { id } = useParams();
    console.log(phone[id].id)
    const dispatch = useDispatch()
    const navigate = useNavigate();

 const handleAddToCart = () => {
    dispatch(addItem({
      id: phone[id].id, 
      imgUrl: phone[id].imgUrl, 
      item: phone[id].title, 
      price: phone[id].price, 
      amount: 1
    }));
    navigate('/cart');
  };

    //숫자단위 콤마
  function formatPrice(price) {
    return new Intl.NumberFormat('ko-KR', { style: 'currency', currency: 'KRW' }).format(price);
  }

    return (
        <div className='detail'>
            <Row>
                <Col md={6}>
                    <img src={process.env.PUBLIC_URL  + phone[id].subimg1} width="100%" />
                    <img src={process.env.PUBLIC_URL  +phone[id].subimg2} width="100%" />
                    <img src={process.env.PUBLIC_URL  +phone[id].subimg3} width="100%" />

                </Col>

                <Col md={6} className='rightdisplay'>
                    <div id='align'>
                        <div className="align">
                            <div className="align-gumaebox">
                                <h4 className='pt-5'>{phone[id].title}</h4>
                                <div className="detailtext">
                                    <p>{formatPrice(phone[id].price)}</p>
                                    <h6 className='pt-55' style={{ whiteSpace: 'pre-wrap' }}>{phone[id].content1}</h6>
                                    <p>{phone[id].content2}</p></div>

                                <div id="button">
                                    <Button className='sel-phone'>
                                        <p>-기종 선택-</p>
                                        <span className='sel-span'>^</span>
                                    </Button>
                                    <Button className='amount'>
                                        <span className='am-left'>-</span>
                                        <span>1</span>
                                        <span className='am-right'>+</span>
                                    </Button>
                                    <div id='order-shop'>
                                    <Button className='shop-ba' onClick={() => handleAddToCart()}>장바구니 담기</Button>
                                    <Button className='order'>주문하기</Button>

                                </div>
                                </div>
                              
                                <div id="nekoicon"><a href="#"><img src={ process.env.PUBLIC_URL +'/img/neko.png'} /></a></div>
                            </div>

                        </div>

                    </div>

                </Col>

          
            </Row>
            <div id='inner'>
                <div className='marquee__inner'>
                    <span className='marquee__text'>
                        <img src={process.env.PUBLIC_URL +"/img/inner.png"} width="100%" alt="" />
                    </span>
                    <span className='marquee__text'>
                    <img src={process.env.PUBLIC_URL +"/img/inner.png"}  width="100%" alt="" />
                    </span>
                    <span className='marquee__text'>
                    <img src={process.env.PUBLIC_URL +"/img/inner.png"} width="100%" alt="" />
                    </span>
                    <span className='marquee__text'>
                    <img src={process.env.PUBLIC_URL +"/img/inner.png"}  width="100%" alt="" />
                    </span>
                    <span className='marquee__text'>
                    <img src={process.env.PUBLIC_URL +"/img/inner.png"} width="100%" alt="" />
                    </span>
                </div>

            </div>
            <div id='bottomimg'>
                <Col md={6} className='bottomimg'>
                    <ul>
                        {/* <li><img src={phone[id].bottombackground} width="100%" /></li> */}
                        <li> <img src={process.env.PUBLIC_URL  + phone[id].bottomimg1} width="100%" />
                        
                            <div>
                                <h6 className='pt-55' style={{ whiteSpace: 'pre-wrap' }}>{phone[id].content3}</h6>
                                <p>{phone[id].content4}</p>
                            </div>
                        </li>

                        <li> <img src={process.env.PUBLIC_URL  + phone[id].bottomimg2} width="100%" />
                            <div>
                                <h6 className='pt-55' style={{ whiteSpace: 'pre-wrap' }}>{phone[id].content5}</h6>
                                <p>{phone[id].content6}</p>
                            </div>
                        </li>

                        <li> <img src={process.env.PUBLIC_URL  + phone[id].bottomimg3} width="100%" />
                            <div>
                                <h6 className='pt-55' style={{ whiteSpace: 'pre-wrap' }}>{phone[id].content5}</h6>
                                <p>{phone[id].content6}</p>
                            </div>
                        </li>

                    </ul>
                </Col>
            </div>

        </div>
    )
}

export default Detail