import { configureStore, createSlice } from '@reduxjs/toolkit'


let user = createSlice({
   name: 'user',
   initialState: { name: 'kim', age: 20 },
   reducers: {
      changeName(state) {
         state.name = 'john kim'
      },
      //state 변경함수를 action이라고 함
      increase(state, action) {
         state.age += action.payload
         //payload : 파라미터 전송해주는 기능
      }
   }
})

export let { changeName, increase } = user.actions


let cart = createSlice({
   name: 'cart',
   initialState: [],
   reducers: {
      addCount(state, action) {
         let idx = state.findIndex((a) => { return a.id === action.payload })
         //console.log(num); 
         ++state[idx].amount;
      },

      subtractcount(state, action) {
         let idx = state.findIndex((a) => { return a.id === action.payload })
         //console.log(num); 
         state[idx].amount--

         if (state[idx].amount < 1) {
            alert('최소 주문 수량은 1개 입니다.');
            state[idx].amount = 1;
         }
      },
      removeItem(state, action) {       
         let idx = state.findIndex(a => a.id === action.payload)
         state.splice(idx, 1)
      },
      sortName(state) {
         state.sort((a, b) =>
            a.item > b.item ? 1 : -1,
         );
      },

      addItem(state, action) {
         let idx = state.findIndex(a => a.id === action.payload.id)
         if (idx >= 0) {
            state[idx].amount++;
         } else {
            state.push(action.payload)
         }
      },
   }
})
// addCount(1) 
export let { addCount, sortName, addItem, subtractcount , removeItem} = cart.actions


export default configureStore({
   reducer: {
      user: user.reducer,
      cart: cart.reducer
   }

}) 
