import React, { useRef } from 'react'
import '../Login.css'
import { Container, Row, Col, Button } from 'react-bootstrap'

const Login = () => {
  const pwref = useRef(null)
  const id =useRef();
  const emailRegEx = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  
  function chkPW() {
    const idIv = id.current.value;
    var pw = pwref.current.value;
    var num = pw.search(/[0-9]/g);
    var eng = pw.search(/[a-z]/ig);
    var spe = pw.search(/[`~!@@#$%^&*|₩₩₩'₩";:₩/?]/gi);

if(!emailRegEx.test(idIv)){
  alert('이메일 형식에 맞게 다시 입력해 주세요');
  id.current.focus();
  return false
}

    if (pw.length < 8 || pw.length > 20) {
      alert("비밀번호는 8자리 ~ 20자리 이내로 입력해주세요.");
      return false;
    } else if (pw.search(/\s/) != -1) {
      alert("비밀번호는 공백 없이 입력해주세요.");
      return false;
    } else if (num < 0 || eng < 0 || spe < 0) {
      alert("비밀번호는 영문,숫자, 특수문자를 혼합하여 입력해주세요.");
      return false;
    } else {
      alert("로그인 완료");
      return true;
    }
  }
  
  return (
    <Container id='component_login'>
      <Row id='login_site'>
        <Col lg={6}>
          <form>
            <div id='loginpage'>
            <p className='id-password login'>LOG IN</p>
            <div className='id-password'>아이디</div>
            <input type='text' name='userid' placeholder='Id' ref={id}></input><br/>
            <div className='id-password'>비밀번호</div>
            <input type="password"name='userpassword' ref={pwref}placeholder='password' id='password'></input><br/>
             <Button variant="dark" size="lg" onClick={chkPW}>Log In</Button>
             </div>
          </form>
         
        </Col>
      </Row>
      <div id="nekoicon"><a href="#"><img src={process.env.PUBLIC_URL + '/img/neko.png'} /></a></div>
    </Container>
  )
}

export default Login;